.section-cities {
  background-color: $light-pink-background-color;
}

.container-cities {
  padding-top: 50px;
  padding-bottom: 50px;
  width: auto;

  background-color: $light-pink-background-color;
  //background-image: url(../images/sectionbg21.png);

  //background-repeat: no-repeat;
  //background-position: center;
  //background-size: cover;
  margin-top: 84px;

  @media screen and (min-width: 768px) {
    padding: 50px 30px;
    margin-top: 0px;
  }

  @media screen and (min-width: 1200px) {
    padding: 55px 43px;
    background-image: url(../images/sectionbg21.png);
    background-repeat: no-repeat;
  }

}

.cities {
  margin-top: -160px;

  @media screen and (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 0;
  }
}

.cities-text {
  background-color: $sand-background-color;
  color: $base-white-color;
  border-radius: 4px;
  text-align: center;
  width: 80px;
}

.cities-item {
  width: 220px;
  background-color: $base-white-color;
  padding: 43px 20px;
  margin-bottom: 30px;
  margin-right: auto;
  margin-left: auto;
  border-radius: 24px;
  margin: -15px;
  margin-bottom: 30px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);


  // @media screen and (max-width: 767px) {
  //   //width: 320px;
  //   margin-right: auto;
  //   margin-left: auto;
  // }

  @media screen and (max-width: 767px) {
    margin-right: auto;
    margin-left: auto;
  }


  @media screen and (min-width: 768px) {
    padding: 30px 30px;
    width: calc((100% - 30px * 3) / 3);
    margin: 15px;
  }

  @media screen and (min-width: 1050px) {
    width: calc((100% - 30px * 3) / 3);
    margin: 15px;
  }
}

.card-item__city {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.8;
  padding-top: 25px;
  padding-bottom: 10px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.86;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.88;
  }
}

.card-item__text {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: #907e82;
  padding-bottom: 25px;
  border-bottom: 1px solid #e1e1e1;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.64;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.81;
  }
}

.cities-text-green {
  background-color: $green-background-color;
  color: $base-white-color;
  display: block;
  border-radius: 4px;
  text-align: center;
  width: 130px;
}

.work-days {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.8;
  padding-top: 25px;
  padding-bottom: 25px;
  border-bottom: 1px solid #e1e1e1;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.86;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.88;
  }
}

.time-day {
  margin-bottom: 10px;
}

.days {
  color: #000000;
  margin: 0;
}

.hours {
  color: $light-pink-text-color;
  margin: 0;
}

.cities-contacts {
  font-size: 12px;
  line-height: 1.5;
  color: #907e82;
  padding-top: 25px;

  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: 1.86;
  }

  @media screen and (min-width: 1200px) {
    font-size: 16px;
    line-height: 1.88;
  }
}

.button {
  align-items: center;
  margin-top: 54px;

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
  }
}

.button-white {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.3;
  display: block;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: 162px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);

  @media screen and (min-width: 768px) {
    margin: 0;
    margin-right: 15px;
  }
}

.button-white:focus,
.button-white:hover {
  color: #fff;
  fill: #fff;
  background-color: #d41443;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
